*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app__page{
    display: flex;
}

.header{
    display: flex; /*bring things in a row  */
    align-items: center; 
    justify-content:space-between;
    padding: 20px;
    position: -webkit-sticky;
    position: sticky; /*It just maintain our header at Top only */
    top: 0; /*it is must to have with position  */
    z-index: 100; 
    background-color: white;
}
.header__logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px; /*To make logo some dist far from menu icon*/
    /* padding: 18px 14px 18px 16px; 
    color: var(--yt-spec-wordmark-text);
    flex: none; */
} 
.header__input{
    display: flex;  /*for different div we have to use display fle and align items property*/
    align-items: center;
    width: 40%;
    border: 1px solid black;
}
.header__inputbutton{
    width: 50px !important;
    background-color: #fafafa;
    border-left: 1px solid lightgrey;
    color: grey;
}
.header__icon{
    margin-right: 5px;

}
.header__input > input{ /*This(>) means inside header input targeting input */
    flex: 1 1;
    border: none;

}
.header__icons{
    display: flex;
    align-items: center;
}


.sidebar{
    /* border: 1px solid blue; */
    flex: 0.2 1; /* flex goes from 0 - 1 only */
}
.sidebar > hr{
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}
.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px 20px;

}
.sidebarRow__icon{
    color: #606060;
    font-size: large !important;
}
.sidebarRow__title{
    flex: 1 1;
    margin-left: 20px;
    font-size: 12px;
    font-weight: 500;
}

.sidebarRow:hover{
    background-color: lightgray;
    cursor: pointer;
}
.sidebarRow:hover > .sidebarRow__icon{
    color: red;
}
.sidebarRow:hover > .sidebarRow__title{
    font-weight: bold;
}
.sidebarRow.selected{
    background-color: lightgray;
}
.sidebarRow.selected > .sidebarRow__icon{
    color: red;
}
.sidebarRow.selected > .sidebarRow__title{
    font-weight: bold;
}
.recommendedVideos{
    border: 1px solid black;
    flex: 0.8 1; /*it is important to give flex here also along with sidebar.css*/
    background-color: #f9f9f9;
    padding: 40px 20px;
    padding-bottom: 0;

}
.recommendedVideos > h2{
    margin-left: 5px;
    margin-bottom: 20px;
}
.recommendedVideos__videos{
    display: flex;
    flex-wrap: wrap;

}
.videoCard{
    margin-bottom: 40px;
    width: 270px;
    

}
.videoCard__thumbnail{
height: 140px;
width: 250px;
}
.video__info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}
.videoCard__text{
    margin-left: 15px;
}
.videoCard__text > h4{
    font-size: 14px;
    margin-bottom: 5px;
}
.videoCard__text > p{
    font-size: 14px;
    color: gray;
}
.videocard__avatar{
height: 30px !important;/*We use avatar as a material ui so override its styling we use !importatnt*/
width: 30px !important;
}
.searchPage{
    flex: 0.8 1;
    background-color: #f9f9f9;
    padding: 20px 20px;
}
.searchPage__filter{
    display: flex;
    align-items: center;
    color: #606060;
    font-size: xx-small !important;
}
.searchPage__filter > h2{
    margin-left: 10px;
}
.searchPage > hr{
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}

.channelRow{
    display: flex;
    align-items: center;
    width: 70%;

}

.channelRow__text{
    display: flex;
    flex-direction: column;

}
.channelRow__text> p{
    color: #606060;
    font-size: small !important;

}
.channelRow__logo{
    height: 120px !important;
    width: 120px !important;
    margin: 10px 60px;
}
.channelRow__text > h4{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.videoRow{
    display: flex;
    margin-bottom: 30px;
    max-width: 700px; 

}
.videoRow > img{
    object-fit: contain;
    width: 246px;
    height: 138px;
}
.videoRow__text{
    margin-left: 14px;
}
.videoRow__subs{
    background-color: lightgray;
    padding: 2px;
}
.videoRow__description{
    margin-top: 20px;
    font-size: 12px;
    color: #606060;
}
.videoRow__subsNumber{
    font-weight: bolder;
    color: rgba(84, 157, 211);

}
.videoRow__headline{
    font-size: 12px;
    color: #606060;
}
